.cssload-square {
	margin: 68px auto;
	width: 19px;
	height: 19px;
	transform: rotate(-45deg);
		-o-transform: rotate(-45deg);
		-ms-transform: rotate(-45deg);
		-webkit-transform: rotate(-45deg);
		-moz-transform: rotate(-45deg);
}

.cssload-square-part {
	position: absolute;
	width: 19px;
	height: 19px;
	z-index: 1;
	animation: cssload-part-anim 0.92s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite alternate;
		-o-animation: cssload-part-anim 0.92s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite alternate;
		-ms-animation: cssload-part-anim 0.92s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite alternate;
		-webkit-animation: cssload-part-anim 0.92s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite alternate;
		-moz-animation: cssload-part-anim 0.92s cubic-bezier(0.445, 0.05, 0.55, 0.95) infinite alternate;
}

.cssload-square-green {
	background: rgb(84,250,212);
	right: 0;
	bottom: 0;
	animation-direction: alternate-reverse;
		-o-animation-direction: alternate-reverse;
		-ms-animation-direction: alternate-reverse;
		-webkit-animation-direction: alternate-reverse;
		-moz-animation-direction: alternate-reverse;
}

.cssload-square-pink {
	background: rgb(233,111,146);
	left: 0;
	top: 0;
}

.cssload-square-blend {
	background: rgb(117,81,125);
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: 2;
	animation: blend-anim 0.92s ease-in infinite;
		-o-animation: blend-anim 0.92s ease-in infinite;
		-ms-animation: blend-anim 0.92s ease-in infinite;
		-webkit-animation: blend-anim 0.92s ease-in infinite;
		-moz-animation: blend-anim 0.92s ease-in infinite;
}





@keyframes blend-anim {
	0% {
		transform: scale(0.01, 0.01) rotateY(0);
		animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
	}
	50% {
		transform: scale(1, 1) rotateY(0);
		animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
	}
	100% {
		transform: scale(0.01, 0.01) rotateY(0);
	}
}

@-o-keyframes blend-anim {
	0% {
		-o-transform: scale(0.01, 0.01) rotateY(0);
		-o-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
	}
	50% {
		-o-transform: scale(1, 1) rotateY(0);
		-o-animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
	}
	100% {
		-o-transform: scale(0.01, 0.01) rotateY(0);
	}
}

@-ms-keyframes blend-anim {
	0% {
		-ms-transform: scale(0.01, 0.01) rotateY(0);
		-ms-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
	}
	50% {
		-ms-transform: scale(1, 1) rotateY(0);
		-ms-animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
	}
	100% {
		-ms-transform: scale(0.01, 0.01) rotateY(0);
	}
}

@-webkit-keyframes blend-anim {
	0% {
		-webkit-transform: scale(0.01, 0.01) rotateY(0);
		-webkit-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
	}
	50% {
		-webkit-transform: scale(1, 1) rotateY(0);
		-webkit-animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
	}
	100% {
		-webkit-transform: scale(0.01, 0.01) rotateY(0);
	}
}

@-moz-keyframes blend-anim {
	0% {
		-moz-transform: scale(0.01, 0.01) rotateY(0);
		-moz-animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
	}
	50% {
		-moz-transform: scale(1, 1) rotateY(0);
		-moz-animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
	}
	100% {
		-moz-transform: scale(0.01, 0.01) rotateY(0);
	}
}

@keyframes cssload-part-anim {
	0% {
		transform: translate3d(-10px, -10px, 0);
	}
	100% {
		transform: translate3d(10px, 10px, 0);
	}
}

@-o-keyframes cssload-part-anim {
	0% {
		-o-transform: translate3d(-10px, -10px, 0);
	}
	100% {
		-o-transform: translate3d(10px, 10px, 0);
	}
}

@-ms-keyframes cssload-part-anim {
	0% {
		-ms-transform: translate3d(-10px, -10px, 0);
	}
	100% {
		-ms-transform: translate3d(10px, 10px, 0);
	}
}

@-webkit-keyframes cssload-part-anim {
	0% {
		-webkit-transform: translate3d(-10px, -10px, 0);
	}
	100% {
		-webkit-transform: translate3d(10px, 10px, 0);
	}
}

@-moz-keyframes cssload-part-anim {
	0% {
		-moz-transform: translate3d(-10px, -10px, 0);
	}
	100% {
		-moz-transform: translate3d(10px, 10px, 0);
	}
}



@media (max-width: 768px) {
  .picture {
    width: 100% !important;
  }
  .img {
    max-width: 100% !important;
  }
  footer {
    height: 40px !important;
  }
  footer p {
    font-size: 15px !important;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

p {
  margin: 0 !important;
}

.wrapper {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.blockMenu {  
  top: 33%;
  /* margin-top: -50%;
  transform: translate(-50%, -50%); */
  font-family: 'Archivo Narrow', sans-serif;
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  flex-direction: column;
  text-align: center;
  position: fixed;
}

.main {
  display: flex;
  /* align-items: center; */
  flex-direction: row;
  justify-content: space-between;
}

.group {
  text-decoration: none;
  outline: none;
  list-style-type: none;
  padding: 0;
  color: gray;
  font-size: 18px;
}

.group li:hover{
  cursor: pointer;
  color: black;
}

.group li:first-child {
  font-weight: bold;
  font-size: 20px;
}

.site-logo a, .site-logo{
  text-decoration: none !important;
  outline: none !important;
  font-family: 'Gayathri', sans-serif;
  font-size: 22px;
  font-weight: bold;  
  color: black;
  cursor: pointer;
  margin: 0;
  padding: 0;
}

.site-logo a:hover, nav div p:hover {
  color: grey;
}

nav div p{
  font-size: 18px;
  cursor: pointer;
}

.picture .img img {
  max-width: 100%;
  height: auto;
} 
.img {
  max-width: 50%;
  height: auto;
  position: relative;
  cursor: pointer;
}
/* 1727 */

.menu {
  display: flex;
  width: 30%;
  justify-content: center;
}

.picture {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}

.first:hover .BlockImg0, .second:hover .BlockImg1, .third:hover .BlockImg2, .fourth:hover .BlockImg3 {
  display: flex;
}

.BlockImg0, .BlockImg1, .BlockImg2, .BlockImg3 {
  display: none;
  background: rgba(255, 255, 255, 0.924);
  position: absolute;
  width: 90%;
  height: 90%;
  margin: 5% 0 0 5%;
  justify-content: center;
  align-items: center;
}

.BlockImg0 p, .BlockImg1 p, .BlockImg2 p, .BlockImg3 p {
  font-family: 'Gayathri', sans-serif;
  font-size: 30px;
  font-weight: 350;
}

footer {
  height: 100px;
  text-align: center;
  width: 100%;
  display: flex !important;
  justify-content: center;
  align-items: center;
}

footer p {
  font-family: 'Dancing Script', regular;
  margin: 0;
  font-size: 30px;
}

.header_mobile {
  position: fixed;
  top: 0;
  background: white;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}